import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
// import { Notifications } from '../../../components';
import MenuBar from '../../../assets/images/MenuBar.svg';
import Sidebar from './Sidebar';
import ProfileDropdown from './ProfileDropdown';
import { HBModalConfirm } from '../../../hbBaseClass';
import { useDispatch, useSelector } from 'react-redux';
import { signOut } from '../../../store/auth/actions';
import { logoMain } from '../../../extensions';
import { env, ENVS } from 'config';
import config from 'config';
import { SUCCESS, useApi } from 'store/callApi';
import { removeStripe } from 'store/auth/actions';

interface Props {
  pathname: string;
}

const Header: React.FC<Props> = (props) => {
  const stripePayment = useSelector((state: any) => state.auth.stripe_payment);
  const [showPopupSignout, setShowPopupSignout] = useState(false);
  const [showPopupPayment, setShowPopupPayment] = useState(false);
  const [showSidebar, setShowSidebar] = useState(false);
  const { pathname } = props;
  const history = useHistory();

  const { callApi } = useApi();
  const groupName = useSelector((state: any) => state.auth.group_name);
  const notifications = useSelector((state: any) => state.notifications);

  const dispatch = useDispatch();

  const handleSignOut = () => {
    setShowPopupSignout(false);
    dispatch(signOut());
  };

  const handleResetPayment = () => {
    const stripeFilter = stripePayment.find((item: any) => item.product_id === 'prod_JoXW23PIXK9Q8j');

    const body: any = {
      customer_id: stripeFilter.customer_id,
      subscription_id: stripeFilter.subscription_id,
      product_id: stripeFilter.product_id,
      price_id: stripeFilter.price_id,
    };
    callApi(
      {
        method: 'delete',
        api: config.rest.payment(),
        body: body,
      },
      (response: any) => {
        const { status } = response;
        if (status === SUCCESS) {
          setShowPopupPayment(true);
        }
      },
    );
  };

  const navlinkItems = [
    {
      value: 'Pflegemagazin',
      active: pathname.includes('/newsfeed'),
      link: '/newsfeed',
      show: groupName === 'user',
      icon: 'newsfeed',
    },
    {
      value: 'Mobilität',
      active: pathname.includes('/mobility'),
      link: '/mobility',
      show: groupName === 'user',
      icon: 'mobility',
    },
    {
      value: 'Beratung',
      active: pathname.includes('/consulting'),
      link: '/consulting',
      show: env !== ENVS.PRD,
      icon: 'consulting',
    },
    {
      value: 'Aktivitäten',
      active: pathname.includes('/activity'),
      link: '/activity',
      show: env !== ENVS.PRD,
      icon: 'activity',
    },
  ];

  const profileItems = [
    {
      icon: 'hb-icon-calendar-person',
      label: 'Persönliche Angaben',
      show: true,
      callback: () => history.push('/profile'),
    },
    {
      icon: 'hb-icon-file-text',
      label: 'Dokumente',
      show: groupName === 'nurse',
      callback: () => history.push('/nurse-documents'),
    },
    {
      icon: 'hb-icon-creditcard',
      label: 'Ihr Abonnement',
      show: groupName === 'user',
      callback: () => {
        history.push('/payment');
      },
    },
    {
      icon: 'hb-icon-creditcard',
      label: 'Reset Payment (Testing)',
      show: groupName === 'user',
      callback: () => handleResetPayment(),
    },
    {
      icon: 'hb-icon-shield-check',
      label: 'Passwort ändern',
      show: true,
      callback: () => history.push('/change-password'),
    },
    {
      icon: 'hb-icon-lock-2-opened',
      label: 'Abmelden',
      show: true,
      callback: () => setShowPopupSignout(true),
    },
  ];

  const handleConfirmPopUpPayment = () => {
    setShowPopupPayment(false);
    const stripeFilter = stripePayment.find((item: any) => item.product_id === 'prod_JoXW23PIXK9Q8j');
    const body: any = {
      customer_id: stripeFilter.customer_id,
      subscription_id: stripeFilter.subscription_id,
      product_id: stripeFilter.product_id,
      price_id: stripeFilter.price_id,
    };

    dispatch(removeStripe(body));
    history.push('/mobility/posture');
  };

  return (
    <div className="UserHeader-container">
      <div className="hb-tablet">
        <Sidebar show={showSidebar} setShow={setShowSidebar} navlinkItems={navlinkItems} profileItems={profileItems} />
      </div>
      <HBModalConfirm
        show={showPopupSignout}
        handleClose={() => setShowPopupSignout(false)}
        title="Abmelden"
        content="Sie wollen sich von Ihrem Account abmelden?"
        up="Ja, abmelden"
        down="Abbrechen"
        upCallback={handleSignOut}
        downCallback={() => setShowPopupSignout(false)}
      />
      <HBModalConfirm
        show={showPopupPayment}
        handleClose={() => setShowPopupPayment(false)}
        title="Zahlung zurücksetzen"
        content="Erfolg löschen"
        up="Bestätigen"
        upCallback={handleConfirmPopUpPayment}
        hideCloseBtn
      />

      <div className="hb-header">
        <div className="hb-wrapper">
          <div className="d-flex justify-content-between align-items-center hb-mx-20" style={{ height: '100%' }}>
            <Link to="/">{logoMain}</Link>
            <div className="hb-navlinks">
              {navlinkItems
                .filter((item) => item.show)
                .map((item) => (
                  <Link key={item.value} className={`hb-navlink ${item.active ? 'active' : ''}`} to={item.link}>
                    {item.value}
                    {item.active && <div className="hb-navlink-circle"></div>}
                    {item.icon === 'activity' && notifications.new > 0 && (
                      <div className="hb-activities-circle small">{notifications.new}</div>
                    )}
                  </Link>
                ))}
            </div>
            <div className="hb-dropdown-profile">
              <ProfileDropdown profileItems={profileItems} />
            </div>
            <div className="hb-menubar" onClick={() => setShowSidebar(true)}>
              <div
                style={{
                  backgroundImage: `url(${MenuBar})`,
                  width: 13,
                  height: 9,
                  backgroundSize: 'cover',
                  backgroundRepeat: 'no-repeat',
                  backgroundPosition: 'center',
                  marginRight: 8,
                }}
              />
              <span>Menü</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
